.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #ffff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.graph-container{
  text-align: center
}

.graph{
  display:flex;
  margin: auto;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
}

.navigation li{
  display:inline; 
  padding: 8px;
}
.navigation ul {
  background-color: #dddd;
  padding: 8px;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
